<template>
	<div id="choose-gift">
		<search-header :hasBack="hasBack"></search-header>
		<div class="header">
			<!--<router-link :to="{ name : 'ChooseActivity',query:{id:festivalAds.id ? festivalAds.id:''} }" tag='div' class="festival">
				<img class="bg-img" :src="festivalAds.imagePath" onerror="this.onerror=null;this.src=''" alt="" />	
				<span class="text">节日推荐</span>
			</router-link>
			<router-link :to="{ name : 'ChooseActivity',query:{id:activityAds.id ? festivalAds.id:''} }" tag='div' class="activity">
				<img class="bg-img" :src="activityAds.imagePath" onerror="this.onerror=null;this.src=''" alt=""/>	
				<span class="text">优惠活动</span>
			</router-link>-->
			
			
			<div class="festival" @click="toChooseActivity(festivalAds)">
				<img class="bg-img" :src="festivalAds.imagePath" onerror="this.onerror=null;this.src=''" alt="" />	
				<span class="text">节日推荐</span>
			</div>
			<div class="activity" @click="toChooseActivity(activityAds)">
				<img class="bg-img" :src="activityAds.imagePath" onerror="this.onerror=null;this.src=''" alt=""/>	
				<span class="text">优惠活动</span>
			</div>
		</div>
		<div class="content">
			
			<div class="category-list" @click="spreadList(0)">
				<img class="bg-img" src="../../assets/chooseGit/price-pic.png"  alt="" v-show="!spreadArr[0]"/>
				<!--<div class="price" v-show="!spreadArr[index]">{{item.name}}</div>-->
				<div class="price" v-show="!spreadArr[0]">价格区间</div>
				<!--展开的遮罩-->
				<div class="mask-wrap" v-show="spreadArr[0]">
					<img class="bg-img" src="../../assets/chooseGit/price-pic.png" alt=""/>
					<span class="category-text">价格区间</span>
					<div class="list-wrap" >
						<ul class="clearfix" v-if="pricesArr.length > 0">
							<template v-for="(child,index) in pricesArr">
								<li :data-id='child.id' @click="toGoodsList(child.id,3)">{{child.name}}</li>
							</template>
						</ul>
					</div>
				</div>
			</div>
			
			<div class="category-list" @click="spreadList(1)">
				<img class="bg-img" src="../../assets/chooseGit/special-pic.png" alt="" v-show="!spreadArr[1]"/>
				<!--<div class="price" v-show="!spreadArr[index]">{{item.name}}</div>-->
				<div class="price" v-show="!spreadArr[1]">特殊日子</div>
				<!--展开的遮罩-->
				<div class="mask-wrap" v-show="spreadArr[1]">
					<img class="bg-img" src="../../assets/chooseGit/special-pic.png" alt=""/>
					<span class="category-text">特殊日子</span>
					<div class="list-wrap" >
						<ul class="clearfix" v-if="specialArr.length > 0">
							<template v-for="(child,index) in specialArr">
								<li :data-id='child.id' @click="toGoodsList(child.id,3)">{{child.name}}</li>
							</template>
						</ul>
					</div>
				</div>
			</div>
			
			<div class="category-list" @click="spreadList(2)">
				<img class="bg-img" src="../../assets/chooseGit/classify-pic.png" alt="" v-show="!spreadArr[2]"/>
				<!--<div class="price" v-show="!spreadArr[index]">{{item.name}}</div>-->
				<div class="price" v-show="!spreadArr[2]">赠礼对象</div>
				<!--展开的遮罩-->
				<div class="mask-wrap" v-show="spreadArr[2]">
					<img class="bg-img" src="../../assets/chooseGit/classify-pic.png" alt=""/>
					<span class="category-text">赠礼对象</span>
					<div class="list-wrap" >
						<ul class="clearfix" v-if="sendGiftArr.length > 0">
							<template v-for="(child,index) in sendGiftArr" >
								<li :data-id='child.id' @click="toGoodsList(child.id,3)">{{child.name}}</li>
							</template>
						</ul>
					</div>
				</div>
			</div>
			
			<div class="category-list" @click="spreadCategory(index)" v-for="(item,index) in categorys" >
				<img class="bg-img" :src="item.imagePath" alt="" v-show="!categorydArr[index]"/>
				<div class="price" v-show="!categorydArr[index]">{{item.name}}</div>
				<div class="mask-wrap" v-show="categorydArr[index]">
					<img class="bg-img" :src="item.imagePath" alt=""/>
					<span class="category-text">{{item.name}}</span>
					<div class="list-wrap" >
						<ul class="clearfix" v-if="item.categorys.length > 0">
							<template v-for="(child,index) in item.categorys" v-if="index<12">
								<li :data-id='child.id' @click="toGoodsList(child.id,1)">{{child.name}}</li>
							</template>
						</ul>
					</div>
				</div>
			</div>
				
		</div>
		<div class="cart-wrap">
			<span class="num-icon">{{cartTotal?cartTotal:'0'}}</span>		
			<img class="cart-icon" src="../../assets/present_btn_shopping_cart@2x.png" @click="linkTo('Cart')"/>
		</div>
	</div>
</template>

<script>
import SearchHeader from '@/components/Header/SearchHeader'
export default {
	data(){
		return{
			cartTotal:0,
			spread:false,
			spreadArr:[],
			categorydArr:[],
			hasBack:false,
			categorys:[],
			festivalAds:{},//节日推荐
			activityAds:{},//优惠活动
			pricesArr:[],//价格区间
			specialArr:[],//特殊日子
			sendGiftArr:[],//送礼对象
			
		}
	},
  components: {
  	SearchHeader
  },
	created(){
		this.getFestivalAds();
		this.getActivityAds();
//		this.getArticleList();
		this.getCartGoodsCount();
		this.getCategoryList();
		this.getPrices();
		this.getSpecialArr();
		this.getSendGiftArr();
		this.spreadArrInit();
	},
	methods:{
//		toChooseActivity(id){
//			this.$router.push({name:'ChooseActivity',query:{id:id}})
//		},
		toChooseActivity(item){
//			this.$router.push({name:'ChooseActivity',query:{id:id}})
				console.log(item)
				if(item.actionType == 1 || item.actionType == 2 || item.actionType == 3){
					window.location.href = item.actionContent
				}
		},
		spreadListInit(){
			let categorys = this.categorys
			for (var i = 0; i < categorys.length; i++) {
				this.categorydArr.push(false)
			}
		},
		spreadArrInit(){
			for (var i = 0; i < 3; i++) {
				this.spreadArr.push(false)
			}
		},
		toGoodsList(categoryId,action){
			let a = action
			let type = 1
			this.$router.push({name:'GoodsList',query:{categoryId,a,type}})			
		},
		getFestivalAds(){
			this.$Api.getAdList(4).then((res)=>{
				console.log(res);
				if(res.q.s == 0){
					if(res.q.ads.length > 0){
						this.festivalAds = res.q.ads[0]
					}
				}
			})
		},
		getActivityAds(){
			this.$Api.getAdList(5).then((res)=>{
				console.log(res);
				if(res.q.s == 0){
					if(res.q.ads.length > 0){
						this.activityAds = res.q.ads[0]
					}
				}
			})
		},
		
		getCategoryList(){
			this.$Api.getCategoryList(1).then((res)=>{
				if(res.q.s==0){
					this.categorys = res.q.categorys;
					this.spreadListInit()
				}
			})
		},
		getArticleList(){
			this.$Api.getArticleList(1).then((res)=>{
				console.log(res)
			})
		},
		spreadList(index){
			this.spreadArr.splice(index,1,!this.spreadArr[index])
		},
		spreadCategory(index){
			this.categorydArr.splice(index,1,!this.categorydArr[index])
		},
		//价格区间
		getPrices(){
  		let key = 'prod.cat.money'
  		this.$Api.getReferenceItemList(key).then((res)=>{
  			console.log(res)
  			if(res.q.s == 0){
  				this.pricesArr = res.q.items
  			}
  		})
  	},
  	//特殊节日
  	getSendGiftArr(){
  		let key = 'prod.cat.festerval'
  		this.$Api.getReferenceItemList(key).then((res)=>{
  			console.log(res)
  			if(res.q.s == 0){
  				this.specialArr = res.q.items
  			}
  		})
  	},
  	//送礼对象
  	getSpecialArr(){
  		let key = 'prod.cat.propose'
  		this.$Api.getReferenceItemList(key).then((res)=>{
  			console.log(res)
  			if(res.q.s == 0){
  				this.sendGiftArr = res.q.items
  			}
  		})
  	},
  	getCartGoodsCount(){
			this.$Api.getCartGoodsCount(1).then((res)=>{
				console.log(res)
				if(res.q.s == 0){
					this.cartTotal = res.q.count
				}
			})
		},
	},

 
}
</script>



<style lang="scss">
	@import "../../sass/baseSass.scss";
	#choose-gift{
		/*height: 100%;*/
		background: #f0f0f0;
		.header{
			padding: 0.27rem 0.13rem;
			display: flex;
			>div{
				position: relative;
				font-size: 0.45rem;
				flex: 1;
				height: 2.67rem;
				line-height: 2.67rem;
				text-align: center;
				color: #fff;
			}
			.festival{
				margin-right: 0.13rem;
				/*background-image: url(../../assets/chooseGit/festival-pic.png);
				background-repeat: no-repeat;
				background-size: 100%;*/
			}
			.activity{
				/*background-image: url(../../assets/chooseGit/activity-pic.png);
				background-repeat: no-repeat;
				background-size: 100%;*/
			}
			.bg-img{
				position: absolute;
				left: 0;
				top: 0;
				width: 100%;
				height: 100%;
			}
			.text{
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%,-50%);
			}
		}
		.content{
			margin-bottom:1.6rem;

			>div{
				width: 100%;
				height: 4.0rem;
				line-height: 4.0rem;
				font-size: 0.45rem;
				margin-bottom: 0.27rem;
				text-align: center;
				color: #c59d76;
				background-repeat: no-repeat;
				background-size: contain;
			}
			.category-list{
				position: relative;
				.bg-img{
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 4.0rem;
				}
				.mask-wrap{
					width: 100%;
					height: 100%;
					background-color: rgba(0, 0, 0, 0.4);
					color:#fff;
					font-size: 0.4rem;
					z-index: 1;
					.bg-img{
						position: absolute;
						top: 0;
						left: 0;
						opacity: 0.4;
					}
					
				}
				.category-text{
					/*position: absolute;*/
					position: relative;
					top: -35%;
					/*left: 50%;*/
					/*transform: translateX(-50%);*/
				}
				.list-wrap{
					/*width: 70%;*/
					width: 100%;
					/*height: 100%;*/
					position: absolute;
					left: 50%;
					transform: translateX(-50%);
					top: 1.47rem;
					>ul{
						li{
							float: left;
							width: 25%;
							min-height: 0.42rem;
							line-height: 0.42rem;
							margin-bottom: 0.4rem;
							@include textflow-one;
						}
					}
				}
			}
			.price{
				/*background-image: url(../../assets/chooseGit/price-pic.png);*/
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%,-50%);
			}
				
			.special{
				background-image: url(../../assets/chooseGit/special-pic.png);
			}
			.classifly{
				background-image: url(../../assets/chooseGit/classify-pic.png);
			}

		}
		.text{
			font-size: box(30);
		}
		.cart-wrap{
			/*position: relative;*/
		}
		.cart-icon{
			position: fixed;
			width: 1.2rem;
			height: 1.2rem;
			right: 0.4rem;
			bottom: 4.36rem;
			z-index: 100;
		}
		.num-icon{
			position: fixed;
			right: 0.5rem;
			bottom: 5rem;
	    font-size: 0.32rem;
	    display: inline-block;
	    min-width: 0.4rem;
	    height: 0.4rem;
	    border-radius: 50%;
	    color: #fff;
	    background: #fe4436;
	    text-align: center;
	    z-index: 101;
		}
	}
</style>


